html{
  font-family: 'Noto Sans', sans-serif;
  height: 100vh;
}

body{
  color: grey;

}

::-webkit-scrollbar {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #000000;
}

.titulo{
  color: black;
}

.write{
  padding: 1.5% 0% 1.5% 1.5%;
  width: 100%;
  border-radius: 10px;
}

.btn-enviar{
  right: 0%;
  top:20%;
  position: absolute;
  background-color: #2D9CDB;
  color: white;
  border-radius: 5px;

}

.position{
  position: relative;
  width: 90%;
  margin-left: 4%;
  margin-top: 1%;
}


.info-box{
  padding: 3%;
  display: flex;
  flex-direction: column;
  margin:2vh auto 0vh auto; 
  width: 50vh;
  height: 44%;
  border-radius: 20px;
  border: 3px groove rgb(186, 187, 186); 
  justify-content: space-between;
}

.newc{
  position: absolute !important;
  top: 25%;
  left: 30% ;
  border-radius: 10px;
  background-color: #120f13;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  z-index: 10000000;
  width: 40%;
  height: 30%;
  color: white;
}

.newc input, .newc textarea{
  padding: 2% 1%;
  margin: 1% 5%;
  opacity: 70%;
  background-color: #3c393f;
  border-radius: 10px;
  width: 86%;

}

.newc p{
  padding-top: 2%;
  padding-left: 5%;
  margin: 0%;
}

.btn-cerrar{
  position: absolute;
  top: 5%;
  right: 6%;
  border-radius: 10px;
  background-color: #120f13;
  color: white;
  border: none;
  font-size: large;
}

.save{
  margin-left: 78%;
  border-radius: 10px;
  color: white;
  border: none;
  background-color: #2D9CDB;
  width: 15%;
  padding: 1% !important;
  margin-bottom: 1.5%;
}

.conversacion{
  height: 84%;
  padding-left: 5%;
  overflow-y: scroll;
}

.conversacion p{
  margin: 0.8%;
}

.volver{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  margin: 0% !important;
  padding: 0% !important;
}


.enviar{
  height: 8%;
}

.msg{
  display: flex;
  flex-direction: column;
  margin-left: 3%;
  width: 88%;
  margin-top: 0.4%;
}

.name{
  padding-left: 5%;
  font-weight: bold;
}


.resalt{
  font-weight: bold;

}

.main-box{
  padding: 3%;
  display: flex;
  flex-direction: column;
  margin:18vh auto 0vh auto; 
  width: 50vh;
  height: 44%;
  border-radius: 20px;
  border: 3px groove rgb(186, 187, 186); 
  justify-content: space-between;
}

.nombre-c{
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  height: 74%;
}


.newc input, .newc textarea{
  color: white;
}
@media (max-width: 680px) {
  .main-box {
    width: 80vw;
    height: 44%;
  }

  .credits{
    width: 80vw !important;
  }

  .options{
    margin-right: 0%;
    z-index: 10000;
  }


  

  .conversacion{
    height: 80%;
  }
  .write{
    height: 7vh;
  }

  .hamb{
    display: block !important;
  }

  .channels{
    width: 80% !important;
    position: absolute !important;
  }

  .exit{
    display: block !important;
  }

  .down{
    width: 84% !important;
  }

  .options2{
    left: 60% !important;
  }

  .newc{
    width: 80%;
    left: 10%;
  }
}

.underl{
  text-decoration: underline;
  color: #173cbe;
  border: none;
  background-color: transparent;
  font-size: 1em;
}

.exit{
  position: absolute;
  right: 10%;
  z-index: 20000;
  top: 2%;
  padding: 0%;
}


.red{
  color: rgb(229, 38, 38) !important;
}

.hamb{
  position: absolute;
  border: none;
  top: 2%;
  left: 1%;
  background-color: transparent;
}



.apps{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 0%;


}

.join{
  margin-bottom: 1vh;
}

.hamb{
  display: none;
}

.padre{
  width: 15vw;
}

.chat2{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 0%;
  height: 100vh;
}

.canal{
  display: flex;
  flex-direction: row;

}

.cha{
  display: flex;
  flex-direction: row;
  width: 95%;
}
.visualizar{
  margin-bottom: 5%;
}

.visualizar button{
  border: none;
  background-color: transparent;
  color:#2D9CDB;

}

.visualizar button:hover{
  color: #173cbe;
}

.header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1% 5%;
  height: 9vh;
}

.channels{
  width: 20%;
  left: 0%;
  position: relative;
  padding-left: 2%;
  background-color: #120f13;
  color: white;
  padding-right: 2%;
  height: 100%;
  z-index: 1000;
}

.short{
  height: 5vh;
  width: 5vh;
  border-radius: 10px;
  margin-right: 5%;
  background-color: #3c393f;
  
}



.inic{
  margin-top: 20%;
}

.short p{
  text-align: center;
  
}

.chat{
  width: 82%;
}

.channels span{
  color: white;
}

.exit{
  display: none;
}

.header span{
  margin-top:5%;
  margin-right: 3%;
}

.options2 span{
  margin-top:18%;
  margin-right: 5%;
}

.basic{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-right: 15vw !important;
  width: 100%;
}



.basic img, .photo{
  border-radius: 10px;
  width: 7vh;
  height: 7vh;
  object-fit: cover;
}

.dev{
  width:18vh;
  margin-bottom: 2vh;
  padding-top: 1vh;
}

.options{
  border: 1px solid rgb(195, 195, 195);
  border-radius: 10px;
  color:rgb(124, 124, 124);
  padding: 0vh 3vh;
  width: 20vh;
  display: flex;
  flex-direction: column;
  margin-top: 0%;
  right: 10%;
  position: absolute;
  background-color: white;
}

.theme2{
  position: absolute;
  right: 1%;
  border: none;
  color: #2D9CDB;
  background-color: transparent;
}

.success{
  background-color: rgb(137, 210, 137);
  height: 10%;
  width: 100%;
  margin-top: 3%;
  border-radius: 10px;
  text-align: center;
  
}

.success p{
  text-align: center;
}

.options p{
  margin-top: 1vh;
  display: inline;
  
}


.line{
  margin: 0%;
  color: #cad1d9;
}


.enter{
  padding: 1vh;
  margin-top: 1vh;
  border-radius: 10px;
  border: 1px groove; 
  background-color: #2F80ED;
  color:white;
}

.back{
  text-decoration: none;
  color: #2D9CDB;
  border: none;
  background-color: transparent;
  margin-left: 47vw;
  padding: 0%;
}

.theme{
  position: absolute;
  top: 3%;
  right: 3%;
  color: #2D9CDB;
  background-color: transparent;
  border: none;
}

.create{
  background-color: transparent;
  border: none;
}

.mas{
  margin-left: 59%;
}

.info{
  display: flex;
  flex-direction: column;
  margin:0% auto 0% auto; 
  width: 60vw;
}

.info h1, .info h2{
  align-self: center;

}

.empieza{
  border:1px solid rgb(195, 195, 195);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2% 5%;
  border-radius: 20px 20px 0px 0px;
}
i{
  margin-block-start: 0.7em;
  margin-block-end: 1em;
  color: black;
}

.save{
  padding: 3% 4%;
  font-size: 1.08rem;
  border-radius: 8px;
}

.edit{
  padding: 1.3% 4%;
  margin-top: 2%;
  height: min-content;
  color:#828282;
  border-radius: 10px;
  border: 1px solid #828282;
  z-index:10;
}
.invisible{
  display: none;
}

form{
  display: flex;
  flex-direction: column;
}

.end{
  border-radius: 0px 0px 20px 20px;
}

.general{
  border:1px solid rgb(195, 195, 195);
  display: flex;
  flex-direction: row;
  padding: 2% 5%;
  justify-content: start;
  overflow-x: hidden;
}

.tema{
  background-color: #173cbe;
  width: 5%;
}

.general:nth-child(2){
  margin-left: 5% !important;
}

.inline{
  display:flex;
  flex-direction: row;
  border: none;
  background-color: transparent;
  color: rgb(195, 195, 195);
  font-size: large;
}

.editImage{
  width: 30%;
}

.photoI{
  background-color: black;
  border-radius: 10px;
  width: 7vh;
  height: 7vh;
}

.primero{
  width: 40%;
}


.key{
  color:rgb(124, 124, 124);
  width: 50%;
}



.grey{
  text-align: center;
}

b{
  font-size: 1.2em;
  text-align: start;
  margin-bottom: 2vh;
}

.change{
  text-decoration: none;
  color:#828282;
  padding-top: 5%;
}
.block{
  display: block;
}
.photo:hover{
  opacity: 50%;
  background: url(https://fonts.google.com/icons?selected=Material%20Icons%3Aphoto_camera%3A) no-repeat left 10px center;
}


.data{
  display: flex;
  flex-direction: row;
  width: 60vw;
  justify-content: space-between;
  margin:0px auto 4% auto; 

}

.user{
  font-weight: bold;
  color: #828282;
}


.time{
  font-size: small;
  padding-top: 0.1%;
  padding-left: 4%;
}

.email{
  background: url(https://raw.githubusercontent.com/gabgg71/authentication-app/87c83002f9531b4695baa86ae9272e5562b2b5f7/public/email.svg) no-repeat left 10px center;
  
}

.buscar{
  background: url(https://raw.githubusercontent.com/gabgg71/authentication-app/87c83002f9531b4695baa86ae9272e5562b2b5f7/public/email.svg) no-repeat left 10px center;
  padding: 3% 3% 3% 14%;
  margin: 6% 0%;
  width: 80%;
  border-radius: 10px;
}

.show{
  width: 5vh;
  height: 5vh;
  margin-top: 1.5%;
  border-radius: 5px;
}

.password{
  background: url(https://raw.githubusercontent.com/gabgg71/authentication-app/87c83002f9531b4695baa86ae9272e5562b2b5f7/public/lock.svg) no-repeat left 10px center;
  
}



.options2{
  position: absolute;
  bottom: 5%;
  left: 16%;
  background-color: aliceblue;
  z-index: 10000;
  padding: 0.3% 1%;
  border-radius: 10px;

  
}



.down{
  position: absolute;
  height: 7%;
  bottom: 0%;
  left: 0%;
  width: 22.7%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  background-color: #070608;
  z-index: 3000;
}

.down button{
  color: white;
}

.form-edit input{
  padding: 1.2vh 1.2vh 1.2vh 1.1vh;

}

.inp-edit{
  border-radius: 7px;
  padding: 2%;
  opacity: 70%;
  border: 1px groove rgb(138, 138, 138);
}

.inp-log{
  border-radius: 7px;
  padding: 2% 2% 2% 12%;
  border: 1px groove rgb(138, 138, 138);
  margin: 2% 0%;
}



p{
  text-align: start;

}

textarea{
  border-radius: 7px;
  opacity: 70%;
  border: 1px groove rgb(138, 138, 138);
  padding: 1.1vh;
  font-family: 'Noto Sans', sans-serif;
  width: 95%;
  
}

.credits{
  display: flex;
  flex-direction: row;
  width: 52vh;
  justify-content: space-between;
  margin:0px auto; 
}


